/* Sticky Footer */
html, body, #root, .app-root { height: 100%; }
.app-root { display: -ms-flexbox; display: flex; -ms-flex-direction: column; flex-direction: column; }
main { -ms-flex: 1 0 auto; flex: 1 0 auto; }
footer { margin-top: 2rem; padding-top: 1rem; padding-bottom: 1rem;}

header {
  background: #cce5ff;
  border-bottom: 1px solid #b8daff;
  color: #004085;
}

.tile {
  text-align: center;
  padding: 0.25rem;
  max-width: 25vh;
}

.tile button {
  width: 100%;
  padding: 1em 0;
}

.tile img {
  width: 100%;
}



